import Vue from 'vue'
import App from './App.vue'

import Notifications from 'vue-notification'
import proxyConfig from './proxies/_config';
import store from './store/index';
import SimpleVueValidation from 'simple-vue-validator';
import router from './router'
import Buefy from 'buefy'
import 'buefy/dist/buefy.css'

Vue.config.productionTip = false

Vue.use(Buefy)

Vue.use(SimpleVueValidation, {
  mode: 'manual',
  templates: {
    error: 'Error.',
    required: 'Requerido.',
    float: 'Debe ser un nmero.',
    integer: 'Debe ser un entero.',
    number: 'Debe ser un nmero.',
    lessThan: 'Debe ser menor a {0}.',
    lessThanOrEqualTo: 'Debera ser menor o igual a {0}.',
    greaterThan: 'Debe ser mayor a {0}.',
    greaterThanOrEqualTo: 'Debera ser mayor o igual a {0}.',
    between: 'Debe estar entre {0} y {1}.',
    size: 'El tamao debera ser {0}.',
    length: 'Debe ingresar {0} caracteres.',
    minLength: 'Como mnimo se aceptan {0} caracteres.',
    maxLength: 'Como mximo se aceptan {0} caracteres.',
    lengthBetween: 'La longitud debera estar entre {0} y {1}.',
    in: 'Debe ser {0}.',
    notIn: 'No debera ser {0}.',
    match: 'No se pueden comparar.',
    regex: 'Formato invlido.',
    digit: 'Debe ser un dgito.',
    email: 'Email invlido.',
    url: 'Url invlida.'
  }
});

Vue.use({
  install(Vue) {
    Object.defineProperty(Vue.prototype, '$validator', {
      value: SimpleVueValidation.Validator
    })
  }
});

Vue.use({
  install(Vue) {
    Object.defineProperty(Vue.prototype, '$proxies', {
      value: proxyConfig
    })
  }
});


Vue.use({
  install(Vue) {
    Object.defineProperty(Vue.prototype, '$user', {
      value: {
        initialize() {
          let token = localStorage.getItem("access_token").split("."),
            user = JSON.parse(
              decodeURIComponent(
                atob(token[1])
                  .split("")
                  .map(c => {
                    return (
                      "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2)
                    );
                  })
                  .join("")
              )
            );

          store.state.user = {
            id: user.nameid,
            userId: user.certserialnumber,
            name: user.unique_name,
            email: user.email,
            roles: user.role
          };
        }
      }
    })
  }
});

Vue.use(Notifications);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
